import React, { useEffect } from 'react';

type Props = {
  toggleRef?: React.RefObject<HTMLDivElement>;
  onEscape?: (e?: KeyboardEvent | MouseEvent) => void;
};

const withEscapeWatcher = <P extends Props>(Component: React.ComponentType<P>) => (props: P) => {
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape') {
        e.stopPropagation();
        if (props.onEscape) {
          props.onEscape(e);
        }
        if (props.toggleRef && props.toggleRef.current) {
          props.toggleRef.current.focus();
        }
      }
    };

    window.addEventListener('keydown', handleKeyDown, true);

    return () => window.removeEventListener('keydown', handleKeyDown, true);
  }, []);

  return <Component {...props} />;
};

export default withEscapeWatcher;
