import { OptionProps } from '@wedgekit/checkbox-list';

type InsideLabelType = {
  options: OptionProps[];
  placeholder: string;
  value: string[];
};

export default ({ options, value, placeholder }: InsideLabelType) => {
  const valueIsOption = options.find((option: OptionProps) => option.id === value[0]);
  if (typeof value === 'undefined' || !valueIsOption) {
    return placeholder;
  }
  return valueIsOption.label;
};
