import React, { useRef, useState, KeyboardEvent } from 'react';
import mergeRefs from 'react-merge-refs';
import { v4 as uuid } from 'uuid';

import { OptionProps } from '@wedgekit/checkbox-list';

import { Chevron } from '@wedgekit/icons';
import InfoButton from '@wedgekit/info-button';
import primitives, { FieldStatusWrapper, Row } from '@wedgekit/primitives';
import { handleKeyEvent } from '@wedgekit/utils';

import { DropdownWrapper, DropdownText, Caret, Label, Badge } from './styled';
import { List } from '../List';
import { DropdownProps } from '../../types';
import { getMultiselectLabel, getSelectLabel } from '../utils';

const Dropdown = React.forwardRef<HTMLDivElement, DropdownProps>((props, forwardedRef) => {
  const { className, ...otherProps } = props;

  if (props.invalid) {
    console.warn(`prop 'invalid' has been deprecated in favor of passing 'status'="error"`);
    console.warn(`To be removed in @wedgekit/select@8.0.0`);
  }

  const internalRef = useRef<HTMLDivElement>(null);
  const focusRef = useRef<HTMLDivElement>(null);
  const dropdownRefs = mergeRefs([forwardedRef, internalRef]);
  const uuidRef = useRef(uuid());

  const [showPopover, setShowPopover] = useState(false);

  const getValueCount = () => {
    if (typeof props.value === 'undefined') {
      return 0;
    }

    const validValues = props.options.map((option: OptionProps) => option.id);
    const values = Array.isArray(props.value) ? props.value : [props.value];
    return values.filter((val) => validValues.includes(val)).length;
  };

  const dropdownProps: {
    'aria-label'?: string;
    'data-cy'?: string;
  } = {};

  if (props.labelHidden) {
    dropdownProps['aria-label'] = props.label;
  }

  const isInternal = (event: React.FocusEvent) =>
    !!focusRef.current &&
    event.relatedTarget instanceof Node &&
    focusRef.current.contains(event.relatedTarget);

  const handleDropdownFocus = (event: React.FocusEvent) => {
    if (props.onFocus && !isInternal(event)) {
      // Only trigger onFocus when focus comes from outside the Dropdown or List
      props.onFocus(event);
    }
  };

  const handleDropdownBlur = (event: React.FocusEvent) => {
    if (props.onBlur && event.currentTarget === event.target && !isInternal(event)) {
      // Only trigger onBlur when the focus moves from the Dropdown to outside the component
      props.onBlur(event);
    }
  };

  const handleListBlur = (event: React.FocusEvent) => {
    // Only trigger onBlur when the focus moves outside the List
    if (props.onBlur) {
      props.onBlur(event);
    }
    if (event.relatedTarget !== internalRef.current) {
      // Let oncClick handle closing the popover when the Dropdown is clicked
      setShowPopover(false);
    }
  };

  return (
    // TODO [JUN-1193] Migrate these props to the label inside of Multiselect and Remove FieldStatusWrapper
    <FieldStatusWrapper
      className={className}
      disabled={props.disabled}
      fullLength
      readOnly={props.readOnly}
    >
      {!props.labelHidden && (
        <Row center>
          <Label disabled={props.disabled} htmlFor={uuidRef.current} required={props.required}>
            {props.label}
          </Label>
          {props.info && <InfoButton info={props.info} tabIndex={props.tabIndex} />}
        </Row>
      )}
      <div ref={focusRef}>
        <DropdownWrapper
          {...dropdownProps}
          labelHidden={props.labelHidden}
          disabled={props.disabled}
          invalid={props.invalid}
          popoverOpen={showPopover}
          readOnly={props.readOnly}
          ref={dropdownRefs}
          role="button"
          status={props.status}
          tabIndex={
            props.disabled || showPopover ? primitives.tabIndex.disabled : props.tabIndex || 0
          }
          onBlur={handleDropdownBlur}
          onFocus={handleDropdownFocus}
          onClick={() => setShowPopover(!showPopover)}
          onKeyPress={(e: KeyboardEvent<HTMLDivElement>) => {
            if (!showPopover) {
              return handleKeyEvent<HTMLDivElement>({
                'Enter|\\s': () => {
                  setShowPopover(true);
                },
              })(e);
            }
            return null;
          }}
        >
          <DropdownText
            htmlFor={uuidRef.current}
            disabled={props.disabled}
            required={props.required}
            isPlaceholder={!props.value || !props.value.length}
          >
            {props.multiple
              ? getMultiselectLabel({
                  options: props.options,
                  placeholder: props.placeholder || 'Select option(s)',
                  value: props.value,
                })
              : getSelectLabel({
                  options: props.options,
                  placeholder: props.placeholder || 'Select one',
                  value: props.value,
                })}
          </DropdownText>
          {props.multiple && !!props.value.length && getValueCount() > 0 && (
            <Badge>{getValueCount()}</Badge>
          )}
          <Caret>
            <Chevron
              color={props.disabled ? 'N400' : 'N600'}
              direction={showPopover && !props.disabled ? 'up' : 'down'}
            />
          </Caret>
        </DropdownWrapper>
        {showPopover && !props.disabled && (
          <List
            {...otherProps}
            id={uuidRef.current}
            toggleRef={internalRef}
            onBlur={handleListBlur}
            onEscape={() => {
              setShowPopover(false);
            }}
            onSelect={
              props.multiple
                ? () => {}
                : (e: React.FocusEvent) => {
                    // Manually trigger list blur to validate after a selection
                    internalRef.current?.focus();
                    setShowPopover(false);
                    if (props.onFocus) {
                      // Refocus to resume `active` status for component
                      props.onFocus(e);
                    }
                  }
            }
            data-cy="multiselect-list"
          />
        )}
      </div>
    </FieldStatusWrapper>
  );
});

export default Dropdown;
