import React, { useEffect, useRef, useState } from 'react';

import Button from '@wedgekit/button';
import { useContainerEvent } from '@wedgekit/hooks';

import { getNewValue, getNewIndex, withEscapeWatcher } from './utils';
import {
  ActionBar,
  Filter,
  MultiselectOption,
  OptionList,
  OptionText,
  OptionsWrapper,
  SelectOption,
  Space,
  StyledPopover,
} from './styled';

import { ListProps, OptionProps } from '../../types';

const List = (props: ListProps) => {
  const {
    actionBar,
    filter: displayFilter,
    filterPlaceholder,
    label,
    multiple,
    options,
    toggleRef,
    value,
    onBlur,
    onChange,
    onEscape,
    onSelect,
  } = props;

  const activeOptionRef = useRef<HTMLInputElement>(null);
  const internalFocusRef = useRef<HTMLDivElement>(null);
  const checkboxListRef = useRef<HTMLDivElement>(null);
  const filterRef = useRef<HTMLInputElement>(null);

  const [filter, setFilter] = useState('');

  const [filteredOptions, setFilteredOptions] = useState(
    options.filter((option) => option.label.toLowerCase().includes(filter.toLowerCase())),
  );

  const [activeOptionIdx, setActiveOptionIdx] = useState(
    !!value === !![] ? 0 : filteredOptions.findIndex((option) => value.includes(option.id)),
  );

  const [allSelected, setAllSelected] = useState(
    value === filteredOptions.map((option) => option.id),
  );

  useEffect(() => {
    activeOptionRef.current?.scrollIntoView({ behavior: 'smooth', block: 'nearest' });
  }, [activeOptionIdx]);

  useEffect(() => {
    if (displayFilter) {
      filterRef.current!.focus();
    } else {
      checkboxListRef.current!.focus();
    }
  }, []);

  useContainerEvent(internalFocusRef, {
    scroll: (e: Event, inside: boolean) => {
      if (!inside && onEscape) {
        onEscape(e);
      }
    },
  });

  const handleFilterChange = (newFilter: string) => {
    setFilter(newFilter);
    setFilteredOptions(
      options.filter((option) => option.label.toLowerCase().includes(newFilter.toLowerCase())),
    );
  };

  const handleClick = (event: React.MouseEvent<HTMLInputElement>) => {
    const option = event.target as HTMLInputElement;

    const currentIdx = parseInt(option.getAttribute('data-idx')!, 10);

    const newValue = getNewValue({
      multiple,
      options,
      filteredOptions,
      currentIdx,
      previousIdx: activeOptionIdx,
      shiftDown: event.shiftKey,
      value,
    });

    onChange(newValue, event);
    if (onSelect) {
      onSelect((event as unknown) as React.FocusEvent);
    }
    setActiveOptionIdx(currentIdx);
  };

  const handleKeyDown = (event: React.KeyboardEvent) => {
    switch (event.key) {
      case 'ArrowDown':
        event.preventDefault();
        setActiveOptionIdx(getNewIndex(activeOptionIdx, 1, filteredOptions.length));
        break;
      case 'ArrowUp':
        event.preventDefault();
        setActiveOptionIdx(getNewIndex(activeOptionIdx, -1, filteredOptions.length));
        break;
      case 'End':
        event.preventDefault();
        setActiveOptionIdx(filteredOptions.length - 1);
        break;
      case 'Home':
        event.preventDefault();
        setActiveOptionIdx(0);
        break;
      case ' ':
      case 'SpaceBar':
        event.preventDefault();
        activeOptionRef.current!.click();
        break;
      default:
    }
  };

  const handleClearAll = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAllSelected(false);
    onChange([], (event as unknown) as React.MouseEvent<HTMLDivElement>);
  };

  const handleSelectAll = (event: React.MouseEvent<HTMLButtonElement>) => {
    if (allSelected) {
      handleClearAll(event);
    } else {
      const allValues = filteredOptions.reduce<string[]>((acc, cur: OptionProps) => {
        acc.push(cur.id);
        return acc;
      }, []);
      setAllSelected(true);
      onChange(allValues, (event as unknown) as React.MouseEvent<HTMLDivElement>);
    }
  };

  const handleBlur = (event: React.FocusEvent) => {
    // Only trigger onBlur when the focus moves outside the List
    const internalFocus =
      !!internalFocusRef.current &&
      event.relatedTarget instanceof Node &&
      internalFocusRef.current.contains(event.relatedTarget);
    if (!internalFocus && onBlur) {
      onBlur(event);
    }
  };

  return (
    <StyledPopover
      node={toggleRef.current!}
      ref={internalFocusRef}
      onExit={onEscape}
      enabled={false}
    >
      <OptionsWrapper data-parent={label} toggleRef={toggleRef} tabIndex={-1} onBlur={handleBlur}>
        {displayFilter && (
          <Filter
            data-parent={label}
            fullWidth
            label={`Filter ${label} select`}
            labelHidden
            placeholder={filterPlaceholder || 'Search'}
            ref={filterRef}
            value={filter}
            onChange={(newFilter) => {
              handleFilterChange(newFilter);
            }}
            onKeyDown={handleKeyDown}
            status="default"
          />
        )}
        <OptionList
          aria-label={label}
          data-parent={label}
          id={`${label}-listbox`}
          role="listbox"
          ref={checkboxListRef}
          tabIndex={displayFilter ? -1 : 0}
          onKeyDown={handleKeyDown}
        >
          {filteredOptions.map((option, idx) =>
            multiple ? (
              <MultiselectOption
                active={activeOptionIdx === idx}
                data-idx={idx}
                data-parent={label}
                disabled={option.disabled}
                id={`${label}-${option.id}`}
                key={`option-${option.id}`}
                label={option.label}
                name={option.id}
                ref={idx === activeOptionIdx ? activeOptionRef : undefined}
                role="option"
                tabIndex={-1}
                value={multiple ? value.includes(option.id) : value[0] === option.id}
                onChange={() => {}}
                onClick={handleClick}
              />
            ) : (
              <SelectOption
                active={activeOptionIdx === idx}
                aria-label={option.label}
                aria-selected={value[0] === option.id}
                data-parent={label}
                data-idx={idx}
                disabled={option.disabled}
                id={`${option.id}`}
                key={`option-${option.id}`}
                ref={idx === activeOptionIdx ? activeOptionRef : undefined}
                role="option"
                tabIndex={-1}
                value={value[0] === option.id}
                onClick={handleClick}
              >
                <OptionText data-parent={label} tabIndex={-1}>
                  {option.label}
                </OptionText>
              </SelectOption>
            ),
          )}
        </OptionList>
      </OptionsWrapper>
      {multiple && !!actionBar && (
        <ActionBar data-parent={label} toggleRef={toggleRef} onBlur={handleBlur}>
          {(typeof actionBar === 'boolean' || actionBar.selectAll) && (
            <Button
              data-parent={label}
              domain="secondary"
              variant="noFill"
              onClick={handleSelectAll}
            >
              {typeof actionBar === 'object' &&
              actionBar.selectAll &&
              typeof actionBar.selectAll === 'string'
                ? actionBar.selectAll
                : 'Select All'}
            </Button>
          )}
          <Space />
          {(typeof actionBar === 'boolean' || actionBar.clearAll) && (
            <Button data-parent={label} domain="primary" variant="noFill" onClick={handleClearAll}>
              {typeof actionBar === 'object' &&
              actionBar.clearAll &&
              typeof actionBar.clearAll === 'string'
                ? actionBar.clearAll
                : 'Clear All'}
            </Button>
          )}
        </ActionBar>
      )}
    </StyledPopover>
  );
};

export default withEscapeWatcher(List);
