import { OptionProps } from '@wedgekit/checkbox-list';

type InsideLabelType = {
  options: OptionProps[];
  placeholder: string;
  value?: string[];
};

export default ({ options, value, placeholder }: InsideLabelType) => {
  if (!value || value.length === 0) {
    return placeholder;
  }

  return value.reduce((acc, cur) => {
    const valueIsOption = options.find((option: OptionProps) => option.id === cur);
    if (!valueIsOption) {
      return acc === '' ? placeholder : acc;
    }
    const name = valueIsOption.label;

    if (acc !== '') {
      return `${acc}, ${name}`;
    }
    return `${name}`;
  }, ``);
};
