import React, { useRef, useState } from 'react';
import { v4 as uuid } from 'uuid';

import { OptionProps } from '@wedgekit/checkbox-list';
import { Chevron } from '@wedgekit/icons';
import InfoButton from '@wedgekit/info-button';
import { FieldStatusWrapper, Row } from '@wedgekit/primitives';

import { Wrapper, NativeDropdown, SelectRaw } from './styled';
import { Caret, DropdownText, Label } from '../Dropdown';
import { DropdownProps } from '../../types';
import { getMultiselectLabel, getSelectLabel } from '../utils';

const NativeSelect = React.forwardRef(
  (props: DropdownProps, forwardedRef: React.Ref<HTMLDivElement>) => {
    const uuidRef = useRef(uuid());

    const [chevronDown, setChevronDown] = useState(true);

    const dropdownProps: {
      'aria-label'?: string;
      'data-cy'?: string;
    } = {};

    if (props.labelHidden) {
      dropdownProps['aria-label'] = props.label;
    }

    return (
      // TODO [JUN-1193] Migrate these props to the label inside of Multiselect and Remove FieldStatusWrapper
      <FieldStatusWrapper
        className={props.className}
        disabled={props.disabled}
        readOnly={props.readOnly}
      >
        {!props.labelHidden && (
          <Row center>
            <Label disabled={props.disabled} htmlFor={uuidRef.current} required={props.required}>
              {props.label}
            </Label>
            {props.info && <InfoButton info={props.info} tabIndex={props.tabIndex} />}
          </Row>
        )}
        <Wrapper>
          <SelectRaw
            multiple
            required={props.required}
            onBlur={(e) => {
              setChevronDown(true);
              if (props.onBlur) {
                props.onBlur(e);
              }
            }}
            onChange={(e) =>
              props.onChange(
                [...e.target.selectedOptions].map((option) => option.value),
                e,
              )
            }
            onFocus={(e) => {
              setChevronDown(false);
              if (props.onFocus) {
                props.onFocus(e);
              }
            }}
            {...dropdownProps}
          >
            {props.options.map((option: OptionProps) => (
              <option
                value={option.id}
                selected={!!props.value.find((value) => value === option.id)}
                disabled={option.disabled}
              >
                {option.label}
              </option>
            ))}
          </SelectRaw>
          <NativeDropdown
            disabled={props.disabled}
            invalid={props.invalid}
            labelHidden={props.labelHidden}
            readOnly={props.readOnly}
            ref={forwardedRef}
            status={props.status}
          >
            <DropdownText
              htmlFor={uuidRef.current}
              disabled={props.disabled}
              required={props.required}
            >
              {props.multiple
                ? getMultiselectLabel({
                    options: props.options,
                    placeholder: props.placeholder || 'Select option(s)',
                    value: props.value,
                  })
                : getSelectLabel({
                    options: props.options,
                    placeholder: props.placeholder || 'Select one',
                    value: props.value,
                  })}
            </DropdownText>
            <Caret>
              <Chevron
                color={props.disabled ? 'N400' : 'N600'}
                direction={chevronDown ? 'down' : 'up'}
              />
            </Caret>
          </NativeDropdown>
        </Wrapper>
      </FieldStatusWrapper>
    );
  },
);

export default NativeSelect;
