import styled from 'styled-components';

import { InputWrapper } from '@wedgekit/input';

import primitives, { getBasePx } from '@wedgekit/primitives';

const OptionWrapper = styled.div<{ toggleRef: React.RefObject<HTMLDivElement> }>`
  overflow-y: auto;
  max-height: 30vh;
  width: ${({ toggleRef }) =>
    toggleRef.current && `${toggleRef.current.clientWidth - primitives.base * 6}px`};
  padding: ${getBasePx(3)};

  ${InputWrapper} {
    margin: 0px;
  }
`;

export default OptionWrapper;
