import React from 'react';

import { browser } from '@dmsi/core';

import { Dropdown, Native } from '../shared';
import { DropdownProps } from '../types';

const Multiselect = React.forwardRef((props: DropdownProps, ref?: React.Ref<HTMLDivElement>) => (
  <>
    {browser.mobile ? (
      <Native {...props} multiple ref={ref} />
    ) : (
      <Dropdown {...props} multiple ref={ref} />
    )}
  </>
));

export default Multiselect;
