import { OptionProps } from '../../../types';

type NewValueType = {
  multiple?: boolean;
  options: OptionProps[];
  filteredOptions?: OptionProps[];
  currentIdx: number;
  previousIdx: number;
  shiftDown: boolean;
  value: string[];
};

const getNewValue = ({
  multiple = false,
  options,
  filteredOptions = options,
  currentIdx,
  previousIdx,
  shiftDown,
  value,
}: NewValueType) => {
  if (!multiple) {
    return [filteredOptions[currentIdx].id];
  }

  const [startChangeIdx, endChangeIdx] = [currentIdx, previousIdx].sort();

  return options.reduce((newValue, option) => {
    const currentlySelected = value.includes(option.id);
    const optionIdx = filteredOptions.findIndex(
      (filteredOption) => filteredOption.id === option.id,
    );

    if (typeof optionIdx !== 'number') {
      if (currentlySelected) {
        return newValue.concat(option.id);
      }
      return newValue;
    }

    if (shiftDown) {
      const addOption = startChangeIdx <= optionIdx && optionIdx <= endChangeIdx;
      if (currentlySelected || addOption) {
        return newValue.concat(option.id);
      }
    }

    const toggleOption = currentIdx === optionIdx;
    const addOption = currentlySelected || toggleOption;
    const removeOption = currentlySelected && toggleOption;

    if (addOption && !removeOption) {
      return newValue.concat(option.id);
    }
    return newValue;
  }, [] as string[]);
};
export default getNewValue;
