import React from 'react';

import { browser } from '@dmsi/core';

import { DropdownProps, SelectProps } from '../types';
import { Dropdown, Native } from '../shared';

const Select = React.forwardRef(
  (
    props: Omit<DropdownProps, keyof SelectProps | 'actionBar'> & SelectProps,
    ref?: React.Ref<HTMLDivElement>,
  ) => {
    const options = props.delible
      ? [
          {
            id: typeof props.delible === 'object' ? props.delible.id : 'DELIBLE_OPTION_ID',

            label: (typeof props.delible === 'object' && props.delible.label) || '---',
          },

          ...props.options,
        ]
      : props.options;

    const value = [props.value];

    const onChange = (
      newValue: string[],
      event: React.MouseEvent<HTMLDivElement> | React.ChangeEvent<HTMLSelectElement>,
    ) => {
      props.onChange(newValue[0] || '', event);
    };

    return (
      <>
        {browser.mobile ? (
          <Native {...props} options={options} ref={ref} value={value} onChange={onChange} />
        ) : (
          <Dropdown {...props} options={options} ref={ref} value={value} onChange={onChange} />
        )}
      </>
    );
  },
);

export default Select;
